// Import the components
import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter } from '@angular/core';
// Imported EsriMapService class component
import { EsriMapService} from './esri-map.service';

//Use @Component decorator to define properties
@Component({
  selector: 'app-esri-map',
  templateUrl: './esri-map.component.html',
  styleUrls: ['./esri-map.component.css']
})

// Export the component, inside and waiting for its OnInit event!
export class EsriMapComponent implements OnInit {

  // Private variables with default values (public properties inside component)
  private _zoom = 10;
  private _center = [0.1278, 51.5074];
  private _basemap = 'streets';

  // Set the @Input decorator on setter/getter methods so that when component is initialized, we dynamically pull from the parent's properties!
  // This exposes specific private variables from above to make this EsriMapComponent modular (by setting properties in its parent component, AppComponent in app.component.ts)
  @Input()
  set zoom(zoom: number) {
    this._zoom = zoom;
  }

  get zoom(): number {
    return this._zoom;
  }

  @Input()
  set center(center: any[]) {
    this._center = center;
  }

  get center(): any[] {
    return this._center;
  }

  @Input()
  set basemap(basemap: string) {
    this._basemap = basemap;
  }

  get basemap(): string {
    return this._basemap;
  }

  // Use @Output decorator to create the custom event (EventEmitter class emits the event 'newListener' when new listeners are added and 'removeListener' when existing listeners are removed.)
  @Output() mapLoaded = new EventEmitter<boolean>();

  // Use @ViewChild decorator to insert the MapView into the DOM
  @ViewChild('mapViewNode') public mapViewEl: ElementRef;

  // Use constructor method to create private class from imported child component, EsriMapService
  constructor(private esriMapService: EsriMapService) { }

  // Now you can pull in props/modules from the esriMapService class with ngOnit()
  public ngOnInit() {

    this.esriMapService.loadMap(this._basemap, this._center, this._zoom, this.mapViewEl)
      .then((r) => {
        // This will cause 'The map loaded: <true or false>' in the console window (see parent, app.component.ts)
        console.log(r);
        this.mapLoaded.emit(true);
      });
  } //ngOnit

}
