/*
Modified/enhanced by Jacob Helfman for San Diego Mesa College course, WEBD 171: Advanced JavaScript (Kris Secor), 11/28/2019. 
Altered basemap/view (EsriMapService class properties), added data layers and widgets using ArcGIS API for Javascript 4.13 (https://developers.arcgis.com/javascript/latest/api-reference/)
*/
/* 
Would be good to write unit tests for this
*/

// Import the components from angular/core folder: ElementRef (wrapper around a native element inside of a View - allows you to work with the DOM by providing the nativeElement) and Injectable, which is a decorator, creating service instances and injecting them into classes
import {ElementRef, Injectable} from '@angular/core';
// Import/install Esri's framework-agnostic utility for loading ArcGIS JS API, included in package.json file (from https://github.com/Esri/esri-loader). This provides us with loadModules() method to serve as a wrapper 
import { loadModules } from 'esri-loader';
//__esri namespace accesses your installed types
import esri = __esri;

// Use @Injectable decorator to inject services into the following EsriMapService class component that's exported
@Injectable()
export class EsriMapService {
  // Create/initialize an blank object within the class
  constructor() { }
  // Load the props/modules using loadMap()
  loadMap(basemap: String, center: Array<number>, zoom: Number, mapContainer: ElementRef) {
    // Wrap loadModules() in a promise
    const promise = new Promise((resolve, reject) => {
      // Load the Esri API's modules via Promise-based pattern
      // loadModules() injects an Esri cdn <script> tag onto the pg, this has replaced the require() method from previous versions' documentation (it's now only used for web pack)
      loadModules([
        'esri/Map',
        'esri/views/MapView', 
        "esri/layers/FeatureLayer",
        "esri/layers/MapImageLayer",
        "esri/widgets/BasemapToggle",
        "esri/widgets/LayerList",
        "esri/widgets/Locate", 
        "esri/widgets/Home",
        "esri/widgets/ScaleBar",
        "esri/widgets/Compass",
        "esri/widgets/Search",
        "esri/widgets/DistanceMeasurement2D",
        "esri/widgets/CoordinateConversion"
      ])
        // In Promise's base call back, set modules' aliases
        .then(([EsriMap, EsriMapView, FeatureLayer, MapImageLayer, BasemapToggle, LayerList, Locate, Home, ScaleBar, Compass, Search, DistanceMeasurement2D, CoordinateConversion]) => {
          // Start your API JS code here
          // Create a Map and define the basemap property, which is passed to your loadMap() method above
          let map: esri.Map = new EsriMap({
            basemap: basemap
          });

          // Initialize the mapView, defining the container (div element from html), center, zoom and map props
          let mapView: esri.MapView = new EsriMapView({
            container: mapContainer.nativeElement,
            center: center,
            zoom: zoom,
            map: map
          });


      /********************
      Add Widgets (BasemapToggle, LayerList, Locate, Home, ScaleBar, Compass, Search)
      ********************/

      const layerList = new LayerList({
        view: mapView,
        // https://developers.arcgis.com/javascript/latest/api-reference/esri-widgets-LayerList-LayerListViewModel.html provides the logic/properties for this widget
        // This was adapted from https://developers.arcgis.com/javascript/latest/sample-code/widgets-layerlist-legend/index.html
        listItemCreatedFunction: function(event) {
          const item = event.item;
          if (item.layer.type != "group") {
            // don't show legend twice
            item.panel = {
              // Add the legend string to the panel of the ListItem class
              content: "legend",
              open: true
            };
          }
        }
      });
      // Adds widget below other elements in the top left corner of the view
      mapView.ui.add(layerList, "bottom-left");

      const locateWidget = new Locate({
        view: mapView   // Attaches the Locate button to the view
      });
      // adds the home widget to the top left corner of the mapView
      mapView.ui.add(locateWidget, "top-left");

      const homeWidget = new Home({
        view: mapView
      });
      // adds the home widget to the top left corner of the mapView
      mapView.ui.add(homeWidget, "top-left");

      // To add the DistanceMeasurement2D widget to the map
      const measurementWidget = new DistanceMeasurement2D({
        view: mapView
      });
      mapView.ui.add(measurementWidget, "bottom-right");      
      const scaleBar = new ScaleBar({
        view: mapView
      });
      // Add widget to the bottom left corner of the view
      mapView.ui.add(scaleBar, "bottom-right");
      
      const ccWidget = new CoordinateConversion({
        view: mapView
      });
      // Adds widget in the bottom left corner of the view
      mapView.ui.add(ccWidget, "top-right");

      const searchWidget = new Search({
        view: mapView
      });
      // Adds the search widget below other elements in
      // the top left corner of the view
      mapView.ui.add(searchWidget, "top-right");

      const basemapToggle = new BasemapToggle({
        view: mapView,  // The view that provides access to the map's "topo-vector" basemap
        nextBasemap: "topo-vector"  // Allows for toggling to the "vector-topo" basemap
        });
      //add the widget to the top right of the view 
      mapView.ui.add(basemapToggle, "top-right");
     
      const compass = new Compass({
        view: mapView
      });
      // adds the compass to the top left corner of the MapView
      mapView.ui.add(compass, "top-right"); 


      /********************
      Add Feature layers (Feature Services) from California State Lands Commission
      and Map Image layers (Map Services) from California Natural Resources Agency
      ********************/

      const habParticularconcern = new FeatureLayer({
        url: "https://services3.arcgis.com/5aaQCuq3e4GRvkFG/ArcGIS/rest/services/Habitat_Areas_of_Particular_Concern/FeatureServer",
        //add the popupTemplate
        popupTemplate: {
                title: "Habitat Areas of Particular Concern",
                content: [
                  {
                    type: "fields",
                    fieldInfos: [
                      {
                        fieldName: "SITENAME_L",
                        visible: true,
                        label: "Site: "
                      },
                      {
                        fieldName: "LTTDT_LINK", 
                        visible: true,
                        label: "Hyperlink for More Info: "
                      }
                    ]
                  }
                ]
         }
      });
      // This layer is not on by default
      habParticularconcern.visible = false;
      map.add(habParticularconcern); 


      const coastalWetlands = new MapImageLayer({
        url: "https://gis.cnra.ca.gov/arcgis/rest/services/Ocean/CSMW_Coastal_Wetlands/MapServer",
        // Map service pop ups need sublayers defined (for some undocumented reason!)
        sublayers: [
          {
            id: 0,
            popupTemplate: {
              title: "Coastal Wetlands (CSMW)",
              content: [
                {
                  type: "fields",
                  fieldInfos: [
                    {
                      fieldName: "WETLAND_TY",
                      visible: true,
                      label: "Wetland Type: "
                    },
                    {
                      fieldName: "NAME",
                      visible: true,
                      label: "County: " 
                    }
                  ]
                }
              ]
            }
          }
        ]
      });
      // Change the name of layer
      coastalWetlands.title = "Coastal Wetlands (from Coastal Sediment Management Workgroup (CSMW))"
      map.add(coastalWetlands);
      
      const coastCriticalHab = new FeatureLayer({
        url: "https://services3.arcgis.com/5aaQCuq3e4GRvkFG/ArcGIS/rest/services/Coastal_Critical_Habitat_Desig/FeatureServer",
        //add the popupTemplate
        popupTemplate: {
          title: "Coastal Critical Habitat Designations",
          content: [
            {
              type: "fields",
              fieldInfos: [
                {
                  fieldName: "CommonName",
                  visible: true,
                  label: "Common Species Name: "
                },
                {
                  fieldName: "ScientificName", 
                  visible: true,
                  label: "Scientific Species Name: "
                },
                {
                  fieldName: "ListingStatus",
                  visible: true,
                  label: "Conservation Status: "
                }
              ]
            }
          ]
         }
      });
      // Change the name of layer
      coastCriticalHab.title = "Coastal Critical Habitat Designations"
      map.add(coastCriticalHab);

      // The following is a very large/slow layer
      const contours = new MapImageLayer({
        url: "https://gis.sandag.org/sdgis/rest/services/Elevation/SD_Region_Contours/MapServer",
      });
      // This layer is not on by default
      contours.visible = false;
      map.add(contours);

      const watersheds = new MapImageLayer({
        url: "https://gis.cnra.ca.gov/arcgis/rest/services/Inland_Waters/Watershed_Boundaries/MapServer",
        //add the popupTemplate
        sublayers: [
          {
            id: 2,
            title: "Subbasins (8-digit hydrologic unit boundaries)",
            popupTemplate: {
              title: "Subbasins (8-digit hydrologic unit boundaries)",
              content: [
                {
                  type: "fields",
                  fieldInfos: [
                    {
                      fieldName: "Name",
                      visible: true,
                      label: "Name: "
                    }
                  ]
                }
              ]
            }
          },
          {
            id: 1,
            title: "Watersheds (10-digit hydrologic unit boundaries)",
            popupTemplate: {
              title: "Watersheds (10-digit hydrologic unit boundaries)",
              content: [
                {
                  type: "fields",
                  fieldInfos: [
                    {
                      fieldName: "Name",
                      visible: true,
                      label: "Name: "
                    }
                  ]
                }
              ]
            }
          },
          {
            id: 0,
            title: "Subwatersheds (12-digit hydrologic unit boundaries)",
            popupTemplate: {
              title: "Subwatersheds (12-digit hydrologic unit boundaries)",
              content: [
                {
                  type: "fields",
                  fieldInfos: [
                    {
                      fieldName: "Name",
                      visible: true,
                      label: "Name: "
                    }
                  ]
                }
              ]
            }
          }

        ]
      });
      map.add(watersheds);

      const impairedStreams = new FeatureLayer({
        url: "https://services3.arcgis.com/5aaQCuq3e4GRvkFG/ArcGIS/rest/services/Impaired_Streams_303d/FeatureServer",
        //add the popupTemplate
        popupTemplate: {
          title: "Impaired Rivers and Streams",
          content: [
            {
              type: "fields",
              fieldInfos: [
                {
                  fieldName: "WBNAME",
                  visible: true,
                  label: "Waterbody Name: "
                },
                {
                  fieldName: "WBTYPE",
                  visible: true,
                  label: "Waterbody Type: "
                }, 
              ]
            }
          ]
        }
      });
      // Change the name of layer
      impairedStreams.title = "Impaired Rivers and Streams - 303d-listed (2014-2016)"
      map.add(impairedStreams);

      const marinas = new FeatureLayer({
        url: "https://services3.arcgis.com/5aaQCuq3e4GRvkFG/ArcGIS/rest/services/Marinas/FeatureServer",
        //add the popupTemplate
        popupTemplate: {
          title: "Marinas",
          content: [
            {
              type: "fields",
              fieldInfos: [
                {
                  fieldName: "Name",
                  visible: true,
                  label: "Name: "
                }
              ]
            }
          ]
        }
      });
      map.add(marinas);

      const diveSites = new FeatureLayer({
        url: "https://services3.arcgis.com/5aaQCuq3e4GRvkFG/ArcGIS/rest/services/Dive_Sites/FeatureServer",
        //add the popupTemplate
        popupTemplate: {
          title: "Dive Sites",
          content: [
            {
              type: "fields",
              fieldInfos: [
                {
                  fieldName: "SiteName",
                  visible: true,
                  label: "Site: "
                },
                {
                  fieldName: "Source", 
                  visible: true,
                  label: "Hyperlink for More Info: "
                }
              ]
            }
          ]
        }
      });
      map.add(diveSites);

      const navAids = new FeatureLayer({
        url: "https://services3.arcgis.com/5aaQCuq3e4GRvkFG/ArcGIS/rest/services/Aids_to_Navigation/FeatureServer",
        //add the popupTemplate
        popupTemplate: {
          title: "Marinas",
          content: [
            {
              type: "fields",
              fieldInfos: [
                {
                  fieldName: "name",
                  visible: true,
                  label: "Name: "
                }
              ]
            }
          ]
        }
      });
      map.add(navAids); 

      const cleanBeachinitiative = new FeatureLayer({
        url: "https://services3.arcgis.com/5aaQCuq3e4GRvkFG/ArcGIS/rest/services/Clean_Beach_Initiative_Project/FeatureServer",
        //add the popupTemplate
        popupTemplate: {
          title: "Clean Beach Initiative Projects",
          content: [
            {
              type: "fields",
              fieldInfos: [
                {
                  fieldName: "DESCRIPTION",
                  visible: true,
                  label: "Description: "
                },
                {
                  fieldName: "LOCATION",
                  visible: true,
                  label: "Location: "
                },
                {
                  fieldName: "URL", 
                  visible: true,
                  label: "Hyperlink for More Info: "
                }
              ]
            }
          ]
        }
      });
      map.add(cleanBeachinitiative);

      const fishingPiers = new FeatureLayer({
        url: "https://services3.arcgis.com/5aaQCuq3e4GRvkFG/ArcGIS/rest/services/Fishing_Piers/FeatureServer",
        //add the popupTemplate
        popupTemplate: {
          title: "Fishing Piers",
          content: [
            {
              type: "fields",
              fieldInfos: [
                {
                  fieldName: "PNT_NAME",
                  visible: true,
                  label: "Pier Name: "
                }
              ]
            }
          ]
        }
      });
      map.add(fishingPiers); 

      const naturalresourceProjects = new MapImageLayer({
        url: "https://gis.cnra.ca.gov/arcgis/rest/services/NRPI/NRPI_points/MapServer",
        sublayers: [
          {
            id: 0,
            popupTemplate: {
              title: "Natural Resource Project Inventory",
              content: [
                {
                  type: "fields",
                  fieldInfos: [
                    {
                      fieldName: "TITLE",
                      visible: true,
                      label: "Title: "
                    },
                    {
                      fieldName: "DESCRIPTIO",
                      visible: true,
                      label: "Description: " 
                    },
                    {
                      fieldName: "RESOURCE_I",
                      visible: true,
                      label: "Resource: " 
                    },
                    {
                      fieldName: "HABITAT",
                      visible: true,
                      label: "Habitat: " 
                    },
                    {
                      fieldName: "LEAD_AGENC",
                      visible: true,
                      label: "Lead Agency: " 
                    },
                    {
                      fieldName: "LINK", 
                      visible: true,
                      label: "Hyperlink for More Info: "
                    }
                  ]
                }
              ]
            }
          }
        ]
      });
      // Change the name of layer
      naturalresourceProjects.title = "Natural Resource Project Inventory (NRPI)"
      map.add(naturalresourceProjects);



          // Resolve the Promise when all of the map is loaded/initialized,...
          mapView.when(() => {
            // All the resources in the MapView and the map have loaded. Now execute additional processes (see parent, EsriMapComponent in esri-map.component.ts)
            resolve('true');
          }, err => {
            console.error(err);
            reject(err);
          });
        })
        .catch(err => {
          console.error(err);
          reject(err);
        });
    });

    return promise;
    }
}
